import React from 'react';
import autoquit from '../assets/autoquit.png';
import { AppVersion } from '../scripts/version';

function Homepage(){
    const appVersion=AppVersion;
    return (
        <div>
            <div style={{ display: 'flex', flexWrap:'wrap', justifyContent:'center'}}>
                <div style={{ minWidth:'300px', width:'45%'}}>
                    <img alt='Autoquit 1.0.0.0' src={autoquit} style={{ width: '100%'}}></img>
                </div>
                <div style={{ minWidth:'300px', width:'50%', verticalAlign:'middle', paddingLeft:'55px', paddingTop:'75px', textAlign:'left'}}>
                    <div style={{ display: 'block', textAlign:'center', marginBottom:"15px", fontSize:'26pt', fontWeight:'bold'}}>Introduction</div>
                    Autoquit is an open-source project developed by Darick Nguyen. The project is mainly written in C# language, and only for Windows OS.
                    <br/>
                    Autoquit help user record the interaction to a specific application and then playback the script later. It offers No-input mode playback, which let the playback do not take control over the mouse or keyboard, let user can run multiple instances of Autoquit as they like.
                    <br/>
                    <br/>
                    The application is built for 32-bit and 64-bit operating system. Requires .NET framework version 4.5.2 or abover.
                    <br/>
                    - Supported OS: Windows 7 or above
                    <br/><br/>
                    <b>Changelogs:</b>
                    {appVersion.map((item)=>
                        <div style={{ marginBottom: 4 }}>
                            <b>Version {item.title}:</b><br/>
                            {item.changelogs.map((info)=>
                                <div>- {info}</div>
                            )}
                        </div>
                    )}
                </div>
            </div>
            <div style={{ marginTop: 45}}>Build with React</div>
        </div>
    );
}

export default Homepage;