import React from 'react';
import Typography from '@material-ui/core/Typography';
import Contacts from '@material-ui/icons/Contacts';
import guide1 from '../assets/1.png';
import guide2 from '../assets/2.png';
import guide3 from '../assets/3.png';

function Feature(){
    return (
        <div style={{ paddingLeft:'15px'}}>
            <Typography variant="h2" component="h2">
                Features Introduction
            </Typography>
            <br />
            <br />
            <img src={guide1} alt='Feature Introduction 1'></img>
            <img src={guide2} alt='Feature Introduction 2'></img>
            <img src={guide3} alt='Feature Introduction 3'></img>
        </div>
    )
}

export default Feature;