import React from 'react';
import Typography from '@material-ui/core/Typography';
import Contacts from '@material-ui/icons/Contacts';
import { FaFacebookSquare } from 'react-icons/fa';
import { FaLinkedin } from 'react-icons/fa';
import { FaTwitterSquare } from 'react-icons/fa';

function Contact(){
    return (
        <div style={{ textAlign:'left', paddingLeft:'55px'}}>
            <Typography variant="h2" component="h2">
            <Contacts style={{fontSize:40 , marginRight:'10px'}} />Contact me
            </Typography>
            <br />
            <br />
            <Typography variant="h6" component="h6">
            Hi there, thank you for interesting in my software. I'm Darick, if you want to send any feedback or contact me, feel free to connect with my Facebook or Twitter.
            <br/>
            If you are in organization and want to give me an offer, please visit my LinkedIn profile below for more information.
            </Typography>
            <br/>
            <Typography variant="body1" component="div">
                <div style={{ height: 30, lineHeight: '34px'}}>
                    <FaFacebookSquare style={{ fontSize: 30, marginRight:'10px', float:'left' }} />Facebook: <a href='https://fb.com/daricvn' target='_blank' rel="noopener noreferrer">Link</a>
                </div>
                <div style={{ height: 30, lineHeight: '34px'}}>
                    <FaTwitterSquare style={{ fontSize: 30, marginRight:'10px', float:'left' }} />Twitter: <a href='https://twitter.com/DarickNguyen' target='_blank' rel="noopener noreferrer">Link</a>
                </div>
                <div style={{ height: 30, lineHeight: '34px'}}>
                    <FaLinkedin style={{ fontSize: 30, marginRight:'10px', float:'left' }} />LinkedIn: <a href='https://www.linkedin.com/in/daricvn/' target='_blank' rel="noopener noreferrer">Link</a>
                </div>
            </Typography>
        </div>
    )
}

export default Contact;